.page {
    display: flex;
    height: 99.6vh;
    width: 100%;
}

@media (orientation: landscape) { 
    .page {
        flex-direction: row;
    }

    .content {
        width: 50%;
        padding-top: min(30vh, 30vw);
        padding-left: min(20vh, 20vw);
    }

    .avatar {
        background-color: #e19d95;
        width: 50%;
    }

    .title {
        font-family: LexendExa-Medium, sans-serif;
        color: #9B5624;
        font-size: min(10vw, 7vh);
        font-weight: 600;
        letter-spacing: 0.2vw;
        margin-top: 5vh;
        margin-bottom: 2vh;
    }

    .subtitle {
        font-family: LexendExa-Medium, sans-serif;
        color: #CD946A;
        font-size: min(5vw, 3vh);
        font-weight: 600;
        letter-spacing: 0.2vw;
    }

    .navItem {
        font-family: LexendExa-Medium, sans-serif;
        font-size: min(6vw, 4vh);
        letter-spacing: 0.2vw;
        color: #575757;
        margin-bottom: 2vh;
    }

    .navItem:hover {
        color: #E98074;
        font-weight: 600;
        cursor: pointer;
    }

    .line {
        content: "";
        flex: 1 1;
        border-bottom: 4px solid #E85A4F;
        margin: auto;
        margin-top: 5vh;
        margin-bottom: 5vh;
    }
}

@media (orientation: portrait) { 
    .page {
        height: 100vh;
        flex-direction: column-reverse;
    }

    .content {
        height: 60%;
        padding-top: min(10vh, 10vw);
        padding-left: min(15vh, 15vw);
    }

    .avatar {
        background-color: #e19d95;
        height: 40%;
        /* padding-left: min(20vh, 20vw); */
    }

    .title {
        font-family: LexendExa-Medium, sans-serif;
        color: #9B5624;
        font-size: min(10vw, 7vh);
        font-weight: 600;
        letter-spacing: 0.2vw;
        margin-top: 5vh;
        margin-bottom: 2vh;
    }

    .subtitle {
        font-family: LexendExa-Medium, sans-serif;
        color: #CD946A;
        font-size: min(5vw, 3vh);
        font-weight: 600;
        letter-spacing: 0.2vw;
    }

    .navItem {
        font-family: LexendExa-Medium, sans-serif;
        font-size: min(6vw, 4vh);
        letter-spacing: 0.2vw;
        color: #575757;
        margin-bottom: 2vh;
    }

    .navItem:hover {
        color: #E98074;
        font-weight: 600;
        cursor: pointer;
    }

    .line {
        content: "";
        flex: 1 1;
        border-bottom: 4px solid #E85A4F;
        margin: auto;
        margin-top: 5vh;
        margin-bottom: 5vh;
    }
}