  @media (orientation: landscape) { 
    .page {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: fit-content;
      height: 99.6vh;
    }
    
    .title {
      font-family: LexendExa-Medium, sans-serif;
      color: #E85A4F;
      font-size: min(10vw, 7vh);
      font-weight: 600;
      letter-spacing: 0.2vw;
      margin-top: 5vh;
      margin-bottom: 4vh;
    }
    
    .about {
      font-family: LexendExa-Medium, sans-serif;
      width: min(70vw, 110vh);
      font-size: min(3vw, 2.5vh);
      line-height: min(4vw, 3vh);
      margin-top: min(2vw, 2vh);
    }
    
    .highlight {
      color: #E98074 !important;
      font-weight: 600 !important;
    }
    
    .bottom {
      padding: min(3vw, 3vh);
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    
    .buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 50%;
  }
  
  .signature {
    width: min(25vw, 25vh);
    height: auto;
  }
}

@media (orientation: portrait) { 
  .page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: fit-content;
    height: 99.6vh;
  }
  
  .title {
    font-family: LexendExa-Medium, sans-serif;
    color: #E85A4F;
    font-size: min(7vw, 7vh);
    font-weight: 600;
    padding-top: min(5vw, 5vh);
    letter-spacing: 0.2vw;
    margin-bottom: 4vh;
  }
  
  .about {
    font-family: LexendExa-Medium, sans-serif;
    width: min(70vw, 110vh);
    font-size: min(2.5vw, 2.5vh);
    line-height: min(3.5vw, 3vh);
    margin-top: min(2vw, 2vh);
    margin-left: auto;
    margin-right: auto;
  }
  
  .highlight {
    color: #E98074 !important;
    font-weight: 600 !important;
  }
  
  .bottom {
    padding: min(3vw, 3vh);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 50%;
  }
  
  .signature {
    width: min(25vw, 25vh);
    height: auto;
  }
}